<script lang="ts">
import { gsap } from 'gsap'
import { computed, defineComponent, inject } from 'vue'

import { lenisInjectionKey } from '@/App.vue'
import CtaSecondary from '@/components/atoms/CtaSecondary.vue'
import IconComponent from '@/components/atoms/IconComponent.vue'
import { useGlobalReducedMotion } from '@/functions/useGlobalReducedMotion'

export default defineComponent({
  name: 'NavItem',
  components: {
    CtaSecondary,
    IconComponent
  },
  setup () {
    const { instance: lenis } = inject(lenisInjectionKey)
    const reducedMotion = useGlobalReducedMotion()
    const scrollSpeed = computed(() => reducedMotion.value ? 0 : 1.2)

    return {
      scrollTop: () => {
        if (lenis.value) {
          lenis.value?.scrollTo(0, { duration: scrollSpeed.value })
        } else {
          gsap.to(window, { scrollTo: 0 })
        }
      }
    }
  }
})
</script>

<template>
  <CtaSecondary
    class="scroll-top"
    monospace
    underline
    @click="scrollTop"
  >
    <slot>{{ $t('Back to top') }}</slot>
    <template #icon-end>
      <div class="icon overflow-hidden">
        <div class="flex flex-col justify-between">
          <IconComponent icon="arrowTop" />
          <IconComponent icon="arrowTop" />
        </div>
      </div>
    </template>
  </CtaSecondary>
</template>

<style lang="scss" scoped>
.scroll-top {
  @include hover {
    &:hover .icon > div {
      transform: translate3d(0%, rem(-20px), 0);
    }
  }
}

.icon {
  width: rem(12px);
  height: rem(12px);

  & > div {
    width: 100%;
    height: rem(32px);
    transition: transform ease-in-out 0.5s;

    @include motion-reduce {
      transition-duration: 0s;
    }
  }

  svg {
    width: rem(12px);
    height: rem(12px);
    color: color('green');
  }
}
</style>
