<script lang="ts">
import { useTicker } from '@monogrid/vue-boost/core'
import StaticImage from '@monogrid/vue-boost/core/StaticImage.vue'
import { getTexture, useDOMToPlane } from '@monogrid/vue-boost/three'
import { templateRef } from '@vueuse/core'
import { computed, defineComponent, inject, toValue, watch } from 'vue'

import { lenisInjectionKey } from '@/App.vue'
import LogoDithered from '@/assets/images/logo-dithered'
import LogoTexture from '@/assets/textures/logo.png'
import { useGlobalReducedMotion } from '@/functions/useGlobalReducedMotion'
import { getMaterialLogo } from '@/three/utils/materialLib'
import { threeEnvironmentInjection } from '@/three/WebGLApp.vue'

export default defineComponent({
  name: 'WebGLLogo',
  components: {
    StaticImage
  },
  props: {
    order: { type: Number, default: 0 },
    z: { type: Number, default: 0 }
  },
  setup (props, { attrs, slots, emit }) {
    const environment = inject(threeEnvironmentInjection)
    if (!environment) throw new Error('[LayerBgPlane] A ThreeEnvironment is not provided')
    const lenis = inject(lenisInjectionKey)
    const el = templateRef<HTMLElement>('el')

    const texture = getTexture(LogoTexture)
    const material = getMaterialLogo(texture)

    const reducedMotion = useGlobalReducedMotion()
    const { mesh, domMapping } = useDOMToPlane(el, {
      material,
      order: () => props.order,
      subdivisions: 1,
      domMappingOptions: {
        z: () => props.z,
        boundingOptions: {
          listenScroll: false
        }
      }
    })
    const enabled = computed(() => !!lenis.instance.value && domMapping.visible.value && !reducedMotion.value)

    function onTick ({ time }) {
      mesh.material.uniforms.uTime.value = time
      mesh.material.uniforms.tTrail.value = environment.globals.cursorTrail.value?.getTarget() || null
    }

    const { toggle } = useTicker(onTick)

    watch(enabled, visible => {
      toggle(visible)
      mesh.visible = visible
    }, { immediate: true })

    watch(() => lenis.instance.value, _lenis => {
      _lenis?.on('scroll', () => domMapping.update())
    }, { immediate: true })

    watch([() => toValue(environment.width), () => toValue(environment.height), () => environment.webgl.renderer], ([w, h, _renderer]) => {
      if (!_renderer) return
      const dpr = _renderer.getPixelRatio() || 1
      mesh.material.uniforms.uResolution.value.set(w * dpr, h * dpr)
    }, { immediate: true })

    return {
      enabled,
      LogoDithered
    }
  }
})
</script>

<template>
  <div ref="el">
    <StaticImage v-if="!enabled" :data="LogoDithered" contain />
  </div>
</template>
