<script lang="ts">
import { templateRef, useElementSize } from '@vueuse/core'
import { gsap } from 'gsap'
import { computed, defineComponent, inject, onUnmounted, watch } from 'vue'

import { lenisInjectionKey, settingsInjectionKey } from '@/App.vue'
import CtaSecondary from '@/components/atoms/CtaSecondary.vue'
import IconComponent from '@/components/atoms/IconComponent.vue'
import LayerBgPlane from '@/components/atoms/LayerBgPlane.vue'
import WebGLLogo from '@/components/atoms/WebGLLogo.vue'
import NavItem from '@/components/molecules/NavItem.vue'
import ScrollTopButton from '@/components/molecules/ScrollTopButton.vue'
import { getFullYear } from '@/utils'

import LanguageSwitcher from '../molecules/LanguageSwitcher.vue'
const menu = [
  { label: 'Home', route: { name: 'Home' } },
  { label: 'About Us', route: { name: 'About' } },
  { label: 'Projects', route: { name: 'Works' } },
  { label: 'Universe', route: { name: 'Universe' } },
  { label: 'Contact Us', route: { name: 'Contact' } }
]

export default defineComponent({
  name: 'SiteHeader',
  components: {
    CtaSecondary,
    IconComponent,
    LayerBgPlane,
    LanguageSwitcher,
    NavItem,
    ScrollTopButton,
    WebGLLogo
  },
  setup () {
    const lenis = inject(lenisInjectionKey)
    const wrapper = templateRef<HTMLElement>('wrapper')
    const content = templateRef<HTMLElement>('content')
    const clip = templateRef<HTMLElement>('clip')
    const clipped = computed(() => !!lenis.instance.value)
    const { height: wrapperHeight } = useElementSize(wrapper)
    const { height: clipHeight } = useElementSize(clip)
    const settings = inject(settingsInjectionKey)
    let tl: gsap.core.Timeline

    watch([wrapperHeight, clipHeight], () => {
      tl?.scrollTrigger.refresh()
      tl?.clear()
      tl?.fromTo(content.value, {
        clipPath: `polygon(0% ${clipHeight.value}px, 100% ${clipHeight.value}px, 100% 100%, 0% 100%)`
      }, {
        clipPath: 'polygon(0% 0px, 100% 0px, 100% 100%, 0% 100%)'
      })
    }, { flush: 'post' })

    watch([clip, clipped], ([trigger, enabled]) => {
      if (!trigger) return
      if (!enabled) {
        tl?.kill()
        tl = undefined
      } else {
        tl = gsap.timeline({
          defaults: { ease: 'none' },
          scrollTrigger: {
            trigger,
            start: 'top bottom',
            end: 'bottom bottom',
            scrub: true
          }
        })
      }
    })

    onUnmounted(() => tl?.kill())

    return {
      clipped,
      menu,
      settings,
      getFullYear,
      onIubendaClick: () => window?._iub?.cs?.api?.openPreferences()
    }
  }
})
</script>

<template>
  <footer
    ref="wrapper"
    class="relative pointer-events-none"
    :class="{ '-mt-lview': clipped }"
  >
    <LayerBgPlane
      class="w-full absolute bottom-0"
      :class="{ 'top-0': !clipped, 'top-lview': clipped }"
      :color-mix="1"
      :z="-10"
      :strength="0"
    />
    <div
      ref="content"
      class="flex flex-col !lt-lg:pt-0 pt-safe-header justify-end min-h-lview"
      :class="{ 'sticky top-0': clipped }"
    >
      <div class="container pb-5 relative pointer-events-auto">
        <h3 class="h3 py-8 lg:max-w-[75vw] lg:py-12">
          {{ settings.footerTitle }}<br />
          <CtaSecondary underline :to="{ name: 'Newsletter' }">
            {{ settings.newsletterCtaLabel || 'Subscribe' }}
          </CtaSecondary>
        </h3>
        <div class="pb-5 lg:pb-8 lg:flex lg:gap-15">
          <div class="lg:flex lg:flex-col lg:flex-shrink-0 lg:justify-start">
            <p>
              <IconComponent class="w-3 mr-2" icon="segment" />
              <span class="label1">MENU</span>
            </p>
            <nav class="mt-2 lg:mt-8 pl-5">
              <ul>
                <li v-for="item in menu" :key="item.route.name">
                  <CtaSecondary
                    class="h5"
                    :class="{ 'active pointer-events-none': $route.name === item.route.name }"
                    :to="item.route"
                  >
                    {{ item.label }}
                  </CtaSecondary>
                </li>
              </ul>
            </nav>
          </div>
          <div class="lt-lg:mt-6 lg:flex lg:flex-col lg:flex-shrink-0 lg:justify-start">
            <p>
              <IconComponent class="w-3 mr-2" icon="segment" />
              <span class="label1 uppercase">{{ $t('External') }}</span>
            </p>
            <nav class="mt-2 lg:mt-8 pl-5">
              <ul>
                <li v-for="link in settings.externalLinks" :key="link.url">
                  <NavItem
                    :href="link.url"
                    target="_blank"
                    rel="nofollow noopener"
                  >
                    {{ link.label }}
                  </NavItem>
                </li>
              </ul>
            </nav>
          </div>
          <div class="lt-lg:mt-6 lg:flex lg:flex-col lg:flex-shrink-0 lg:justify-start">
            <p>
              <IconComponent class="w-3 mr-2" icon="segment" />
              <span class="label1 uppercase">{{ $t('Contact') }}</span>
            </p>
            <div class="flex flex-col gap-5 mt-2 lg:mt-8 pl-5">
              <p v-for="office, i in settings.offices" :key="i">
                <strong class="block mb-1">{{ office.city }}, {{ office.country }}</strong>
                <span v-if="office.address" class="block">{{ office.address }}</span>
                <CtaSecondary
                  class="menu-item"
                  :href="`mailto:${office.mail}`"
                  underline
                >
                  {{ office.mail }}
                </CtaSecondary>
              </p>
              <p>
                <strong class="block mb-1">{{ $t('Jobs') }}</strong>
                <CtaSecondary
                  v-if="settings.jobsEmail"
                  :href="`mailto:${settings.jobsEmail}`"
                  underline
                >
                  {{ settings.jobsEmail }}
                </CtaSecondary>
              </p>
              <div v-if="$breakpoints.ltLg" class="label1">
                <p>-</p>
                <LanguageSwitcher class="mt-1 mb-2" />
                <p>
                  VAT ID 06640500481<br />
                  Cap. Soc. 60.000,00 €
                </p>
                <CtaSecondary
                  v-if="settings.privacyPolicy?.file"
                  class="mt-2"
                  :href="`/media/${ settings.privacyPolicy.file.id }`"
                  target="_blank"
                  rel="nofollow noopener"
                  underline
                >
                  {{ settings.privacyPolicy.label || 'Privacy Policy' }}
                </CtaSecondary>
                <br />
                <CtaSecondary
                  v-if="settings.cookiePolicy?.url"
                  class="mt-2"
                  :href="settings.cookiePolicy.url"
                  target="_blank"
                  rel="nofollow noopener"
                  underline
                >
                  {{ settings.cookiePolicy.label || 'Cookie Policy' }}
                </CtaSecondary>
                <br />
                <CtaSecondary
                  underline
                  class="mt-2"
                  @click="onIubendaClick"
                >
                  {{ $t('Cookie settings') }}
                </CtaSecondary>
              </div>
            </div>
          </div>
          <div v-if="$breakpoints.lg" class="flex flex-col items-end justify-between ml-auto text-right">
            <ScrollTopButton />
            <LanguageSwitcher />
            <p class="pt-6 label1">
              - <br /><br />-- <br /><br />- <br /><br />
              ©{{ getFullYear() }} ALL COPYRIGHTS<br />RESERVED BY MONOGRID
              <br /><br />
              VAT ID 06640500481<br />
              Cap. Soc. 60.000,00 €
            </p>
            <div class="flex flex-wrap gap-5 mt-5 justify-end label1">
              <CtaSecondary
                v-if="settings.privacyPolicy?.file"
                :href="`/media/${ settings.privacyPolicy.file.id }`"
                target="_blank"
                rel="nofollow noopener"
                underline
              >
                {{ settings.privacyPolicy.label || 'Privacy Policy' }}
              </CtaSecondary>
              <CtaSecondary
                v-if="settings.cookiePolicy?.url"
                :href="settings.cookiePolicy.url"
                target="_blank"
                rel="nofollow noopener"
                underline
              >
                {{ settings.cookiePolicy.label || 'Cookie Policy' }}
              </CtaSecondary>
              <CtaSecondary
                underline
                @click="onIubendaClick"
              >
                {{ $t('Cookie settings') }}
              </CtaSecondary>
            </div>
          </div>
          <div v-else class="flex items-end justify-between mt-10">
            <p class="label-1">
              ©{{ getFullYear() }} ALL COPYRIGHTS<br />RESERVED BY MONOGRID
            </p>
            <ScrollTopButton />
          </div>
        </div>
        <div class="logo w-full">
          <WebGLLogo class="w-full h-full" :z="-9" />
        </div>
      </div>
    </div>
    <div v-if="clipped" class="h-lview" />
    <div v-if="clipped" ref="clip" class="w-full h-lview absolute top-lview" />
  </footer>
</template>

