<script lang="ts">
import { FormKit } from '@formkit/vue'
import { getTranslationsInjectionKey } from '@monogrid/vue-boost/i18n'
import { defineComponent, inject } from 'vue'
import { useRouter } from 'vue-router'

import FormProvider from '../atoms/FormProvider.vue'
export default defineComponent({
  name: 'LanguageSwitcher',
  components: {
    FormKit,
    FormProvider
  },
  setup (props, { attrs, slots, emit }) {
    const locales = inject(getTranslationsInjectionKey)()
    const router = useRouter()
    const onChange = (event) => {
      router.push({ params: { locale: event.target.value } })
    }

    return {
      options: Object.entries(locales).map(([key, value]) => ({ label: value.name + (key === 'en' ? '' : '*'), value: key })),
      onChange
    }
  }
})
</script>

<template>
  <div class="f-helvetica">
    <FormProvider>
      <FormKit
        :value="$i18n.locale || 'en'"
        type="select"
        name="lang"
        outer-class="!w-30"
        :options="options"
        :ignore="true"
        validation="required"
        @change="onChange"
      />
      <p class="mt-1 normal-case">*AI Intelligence</p>
    </FormProvider>
  </div>
</template>

<style lang="scss" scoped>
::v-deep(.formkit-messages){
  display: none;
}
</style>
